<template>
  <div id="detail">
    <detailnavbar></detailnavbar>
    <scroll class="content" ref="scroll"
            :probeType="3"
            @scroll="contentscroll">
      <detailswiper :dswiperimg="detailswiper"></detailswiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-goods-info :detail-info="detailinfo"
                         @imageload="imgload"></detail-goods-info>
      <detail-param-info :param-info="paramInfo"></detail-param-info>
      <detail-comment-info :comment-info="commentinfo"></detail-comment-info>
      <goods :goods="recommends"></goods>
    </scroll>
    <backtop @click.native="top" v-show="isshow"></backtop>
  </div>
</template>

<script>
import detailnavbar from "@/views/detail/detailchild/detailnavbar";
import backtop from "@/components/content/backtop/backtop";
import scroll from "@/components/common/scroll/scroll";
import DetailBaseInfo from "@/views/detail/detailchild/DetailBaseInfo"
import DetailShopInfo from "@/views/detail/detailchild/DetailShopInfo";
import DetailGoodsInfo from "@/views/detail/detailchild/DetailGoodsInfo";
import DetailCommentInfo from "@/views/detail/detailchild/DetailCommentInfo";
import DetailParamInfo from "@/views/detail/detailchild/DetailParamInfo";
import detailswiper from "@/views/detail/detailchild/detailswiper";
import {getdetaildata ,Goods,Shop,GoodsParam,getdetailtj} from "@/network/detail"
import goods from "@/components/content/goos/goods";
import {imgloag} from "@/components/content/mixin/mixin";

export default {
  name: "detail",
  components:{
    detailnavbar,
    detailswiper,
    scroll,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    backtop,
    goods
  },
  data(){
    return{
      iid:null,
      detailswiper:[],
      goods:{},
      shop:{},
      detailinfo:{},
      paramInfo:{},
      commentinfo:{},
      isshow:false,
      recommends:[],
    }
  },
  created() {
    this.iid=this.$route.params.iid
    getdetaildata(this.iid).then(res=>{
      this.detailswiper=res.data.result.itemInfo.topImages;
      console.log(res.data.result);
      this.goods=new Goods(res.data.result.itemInfo,res.data.result.columns,res.data.result.shopInfo.services)
      this.shop=new Shop(res.data.result.shopInfo)
      this.detailinfo=res.data.result.detailInfo
      this.paramInfo=new GoodsParam(res.data.result.itemParams.info,res.data.result.itemParams.rule)
      console.log(res.data.result.itemParams);
      if(res.data.result.rate.cRate!==0){
        this.commentinfo=res.data.result.rate.list[0]
        console.log(res.data.result.rate.list);
        console.log(this.commentinfo);
      }
    })
    getdetailtj().then(res=>{
      this.recommends=res.data.data.list
      console.log(res.data.data.list);
    })
  },
  mixins:[imgloag],

  destroyed() {
    this.$bus.$off('imagelode',this.allimgLoad)
  },

  methods:{
    imgload(){
      this.$refs.scroll.refresh()
      console.log(11);
    },
    top(){
      this.$refs.scroll.scrollTo(0,0,500)
    },
    contentscroll(position){
      this.isshow=-position.y>1000
    }
  }
}
</script>

<style scoped>
#detail{
  position: relative;
  z-index: 9;
  background-color: white;
  height: 100vh;
}
.content{
  position: absolute;
  left: 0;
  right: 0;
  top: 44px;
  bottom: 49px;
  overflow: hidden;
}
</style>
